
<template>
  <div class="tb-full-calendar-header">
    <div class="tb-header-left">
      <slot name="header-left">
      </slot>
    </div>
    <div class="tb-header-center">
      <span class="prev-month" @click.stop="goPrev">{{leftArrow}}</span>
      <div class="tb-date_txt" @click.stop="selectDate">
        <span class="y">2018</span>年
        <span class="m">{{month}}</span>月
      </div>
      <div class="date_set" style="display: none">
        <select id="tb-year">
          <option value="2012">2012</option>
          <option value="2013">2013</option>
          <option value="2014">2014</option>
          <option value="2015">2015</option>
          <option value="2016">2016</option>
          <option value="2017">2017</option>
          <option value="2018">2018</option>
          <option value="2019">2019</option>
        </select>
        <select id="tb-month">
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
          <option value="5">5</option>
          <option value="6">6</option>
          <option value="7">7</option>
          <option value="8">8</option>
          <option value="9">9</option>
          <option value="10">10</option>
          <option value="11">11</option>
          <option value="12">12</option>
        </select>
      </div>
      <span class="next-month" @click.stop="goNext">{{rightArrow}}</span>
    </div>
    <div class="tb-header-right">
      <slot name="header-right">
      </slot>
    </div>
  </div>
</template>

<script type="text/javascript">
  import dateFunc from './dateFunc'
  import moment from 'moment';
  export default {
    props: {
      currentMonth: {
        type: [String, Number],
        required: true
      },
      currentYear: {
        type: [String, Number],
        required: true
      },
      locale: {}
    },
    data () {
      return {
        leftArrow  : '<',
        rightArrow : '>',
        month: '',
        year: ''
      }
    },
    computed: {

    },
    methods: {
      goPrev () {
        var newMonth = moment(this.currentMonth).subtract(1, 'months').startOf('month');
        this.month = newMonth;
        this.$emit('change', newMonth);
      },
      goNext () {
        var newMonth = moment(this.currentMonth).add(1, 'months').startOf('month');
        this.month = newMonth;
        console.log(newMonth);
        this.$emit('change', newMonth);
      },
      selectDate () {
        console.log(111);
      }
    }
  }
</script>
<style>
  .tb-full-calendar-header {
      display: flex;
      align-items: center;
    }
    .tb-header-left, .tb-header-right {
      flex: 1;
    }
    .tb-header-center {
      text-align: center;
    }
    .tb-header-center .prev-month, .tb-header-center .next-month {
      cursor: pointer;
      float: left;
      margin: 0 20px;
    }
    .tb-date_txt,.tb-date_set {
      float: left;
    }
    .tb-header-center .title {
      margin: 0 10px;
    }
</style>


